$cell-border-size: 0.5px;
$cell-border-color: rgba(0, 0, 0, 0.25);
$cell-border-edge-radius: 2px;
$cell-border-edge-type: solid;
$cell-content-color: #222;

$task-background-positive: #e8f5e9;
$task-background-positive-partdone: #c8e6c9;
$task-background-positive-fulldone: #a5d6a7;
$task-background-negative: #ffebee;
$task-background-negative-partdone: #ffcdd2;
$task-background-negative-fulldone: #ef9a9a;
$task-background-resource: #fffde7;
$task-background-resource-partdone: #fff9c4;
$task-background-resource-fulldone: #fff59d;
$task-background-neutral: #fafafa;
$task-background-neutral-partdone: #f5f5f5;
$task-background-neutral-fulldone: #eeeeee;

// there are no empty cells in the past
// they are all appear as zero
$task-background-empty-today: white;

$task-background-zero: rgb(238, 238, 238);
$task-color-zero: gray;

$cell-width: 50px;
$task-row-height: 50px;
$cell-height: $task-row-height;
$drag-width: 20px;
$border-color: $cell-border-color;
$title-background: $task-background-neutral;
$title-padding-left: 2px;
$title-min-width: 170px;

.cell-actions {
    position: relative;
    left: -$cell-width;
    width: calc($cell-width * 3);
    display: flex;
    z-index: 1000;

    .action-plus {
        background-color: lightgray;
    }

    .action-minus {
        background-color: lightgray;
    }

    .cell-action {
        width: $cell-width;
        height: $task-row-height;

        button {
            border: $cell-border-size solid black;
            text-align: center;
            width: $cell-width;
            height: $task-row-height;

            &.hover {
                font-weight: bold;
                background-color: #aaa;
            }
        }
    }
}


.task-table {

    .stat-cell-head-title {
        width: $cell-width;
        height: $cell-height;
        box-sizing: border-box;
        border-bottom: $cell-border-size solid $border-color;
        color: $border-color;
        background: white;
        line-height: $cell-height;
        vertical-align: middle;
        text-align: center;
    }

    .stat-cell {
        width: $cell-width;
        height: $cell-height;
        box-sizing: border-box;
        border-bottom: $cell-border-size solid $border-color;
        color: $border-color;
        background: white;
        line-height: $cell-height;
        vertical-align: middle;
        text-align: center;
        font-size: 80%;
    }

    .tasks {
        .sortable>:last-child .stat-cell {
            border-bottom: none;
        }
    }

    .task-row {

        .drag-handler {
            width: $drag-width;
            transform: translate(0, 50%);

            .circle-column span.circle {
                background-color: $border-color;
            }
        }

    }

    .table-head {
        display: flex;
        background: white;
        user-select: none;
    }

    .context-menu-is-showing .date-cell {
        background-color: lightblue;
    }


    .date-cell {
        height: $task-row-height;
    }

    .cell {
        width: $cell-width;
    }

    .task-title-column {
        min-width: $title-min-width;
        // border-right: $cell-border-size solid $border-color;
        // border-bottom: $cell-border-size solid $border-color;
        box-sizing: border-box;

        border-style: solid;
        border-image-slice: 1;
        border-width: $cell-border-size;
        border-image-source: linear-gradient(to left, $cell-border-color, transparent);

        border-left: none;
        border-top: none;

    }

    .table-head .task-title-column {
        border-style: solid;
        border-image-slice: 1;
        border-width: $cell-border-size;
        // border-image-source: linear-gradient(to top, transparent 0%, $cell-border-color 50%, transparent 100%);
        border-image-source:
            linear-gradient(-15deg, $cell-border-color 0 0, transparent 50% 50%);
        border-top: none;
        border-left: none;

        // background: linear-gradient(-10deg, $cell-border-color 0 0, transparent 50% 50%);
        // border-bottom: none;
    }

    .date-cell {
        // border-right: $cell-border-size solid $border-color;
        // border-bottom: $cell-border-size solid $border-color;
        // background-color: lightgray;
        box-sizing: border-box;
        text-align: center;
        width: $cell-width;
        min-width: $cell-width;

        border-style: solid;
        border-image-slice: 1;
        border-width: $cell-border-size;
        // border-image-source: linear-gradient(to top, transparent 0%, $cell-border-color 50%, transparent 100%);
        border-image-source: linear-gradient(to top, $cell-border-color 50%, transparent 100%);
        border-top: none;
        // border-bottom: none;
        border-left: none;

        color: $cell-content-color;

        &:not(.today) {
            color: $border-color;
        }

        &.wd0 {
            * {
                font-weight: bolder;
            }
        }
    }

    .days-wrapper {
        .days {
            display: flex;
        }
    }

    .task-row {
        display: flex;
        padding: 0;
        margin: 0;
        height: $task-row-height;
        background: white;
        user-select: none;

        .task-title-column {
            display: flex;
            box-sizing: border-box;
        }

        .title {
            height: 100%;
            width: 100%;

            input {
                height: 100%;
                width: 100%;
                background: $title-background;
                padding-left: $title-padding-left;
            }

            // border-left: $cell-border-size solid $border-color;
        }

        .points {
            height: 100%;
            display: flex;

            input {
                width: 25px;
                height: 100%;
                background: $title-background;
                font-size: 10px;
                text-align: right;
                padding-right: 5px;
                color: $border-color;

                &.zero {
                    color: transparent;

                    &:hover, &:focus {
                        color: black;
                    }
                }
            }
        }

        .measures {
            height: 100%;
            display: flex;

            .measure-part-done {
                background: $task-background-positive-partdone;
            }

            .measure-full-done {
                background: $task-background-positive-fulldone;
            }
                
            .measure-zero {
                background: $task-background-zero;
                color: $task-color-zero;
            }

            .measure-nan {
                background: repeating-linear-gradient(30deg, rgba(0, 0, 0, 0.15), rgba(0, 0, 0, 0.15) 2px, rgba(0, 0, 0, 0.1) 2px, rgba(0, 0, 0, 0.1) 4px) !important;
                color: transparent;
            }

            .measure-empty.today {
                background: $task-background-empty-today;
            }

            .measure-empty:not(.today) {
                background: $task-background-zero;
                color: $task-color-zero;
            }
        }

        .cell {
            height: 100%;
            box-sizing: border-box;
            border-right: $cell-border-size solid $border-color;
            border-bottom: $cell-border-size solid $border-color;

            input {
                width: 100%;
                height: 100%;
                text-align: center;
            }

        }
    }

    .table-footer {
        .task-title-column {
            height: $task-row-height;
            display: flex;

            .margin {
                display: inline-block;
                width: $drag-width;
                box-sizing: border-box;
                height: $task-row-height;
            }

            .title {
                display: inline-block;
                width: calc(100% - $drag-width);
                height: 100%;
                box-sizing: border-box;

                input {
                    width: 100%;
                    height: 100%;
                    padding-left: $title-padding-left;

                    &::placeholder {
                        color: $border-color;
                    }
                }

                border-right: $cell-border-size solid $border-color;
                border-image: linear-gradient(to bottom, $border-color, rgba(0, 0, 0, 0)) 1 100%;
            }

            border-right: none;
            border-bottom: none;
        }
    }
}


.stat-cell-head-title {
    position: relative;

    button.custom-button.current-stat-title {
        position: absolute;
        left: 0;
        font-size: 80%;
        width: $cell-width;
        height: $cell-height;
        text-align: center;
    }
}

.change-stat-overlay {
    position: relative;
    left: calc(-2 * $cell-width - 1px);
    width: calc($cell-width * 3);
    display: flex;
    z-index: 1000;

    .action-button {
        min-width: $cell-width;
        width: $cell-width;
        min-height: $cell-height;
        height: $cell-height;
        box-sizing: border-box;
        border-left: 1px solid $border-color;
        border-bottom: 1px solid $border-color;
        background: white;
        font-size: 80%;

        &.hover {
            font-weight: bold;
            background-color: #aaa;
        }
    }
}